.content{box-shadow:0 8px 8px 0 rgba(22,45,61,0.12),0 3px 24px 0 rgba(22,45,61,0.18);border-radius:8px;background-color:white;width:600px}.fullscreen-content{height:calc(100% - 96px);width:calc(100% - 96px);max-width:1254px;display:flex;flex-direction:column}.body{font-family:"HelveticaNeueW01-45Ligh","HelveticaNeueW02-45Ligh","HelveticaNeueW10-45Ligh","Helvetica Neue","Helvetica","Arial","メイリオ, meiryo","ヒラギノ角ゴ pro w3","hiragino kaku gothic pro","sans-serif";color:#2B5672;font-size:16px;line-height:24px;padding:36px 30px 12px 30px;box-sizing:border-box}.body.no-padding{padding:0}.fullscreen-body{margin-bottom:auto;overflow:auto}.scrollable{overflow-y:auto}.scrollable::-webkit-scrollbar{width:18px;background-color:#fff}.scrollable::-webkit-scrollbar-button{display:none;height:6px}.scrollable::-webkit-scrollbar-thumb{background-color:#C1E4FE;background-clip:content-box;border-radius:10px;border:5px solid transparent;height:72px}.scrollable::-webkit-scrollbar-thumb:hover{background-color:#4EB7F5}.scrollable::-webkit-scrollbar-thumb:active{background-color:#3899EC}.scrollable.footer-border:not(.no-footer){border-bottom:1px solid #DFE5EB}.no-footer{padding-bottom:42px}
