.root{display:flex;align-items:center;justify-content:center;padding:15px 23px;background:#3899EC;border-radius:8px 8px 0 0;color:#fff}.caption{padding:0 6px;color:#fff;font-weight:300;font-family:"HelveticaNeueW01-45Ligh","HelveticaNeueW02-45Ligh","HelveticaNeueW10-45Ligh","Helvetica Neue","Helvetica","Arial","メイリオ, meiryo","ヒラギノ角ゴ pro w3","hiragino kaku gothic pro","sans-serif";font-size:16px;line-height:1.13}.arrow{z-index:1;transition:background-color 100ms;border-radius:50%}.arrow:hover:not(:active){background-color:#4EB7F5}.arrow-icon{display:block;cursor:pointer;width:30px;height:30px}.arrow-left{margin-right:auto}.arrow-right{margin-left:auto}:global(.rtl) .arrow-icon{transform:scaleX(-1)}:global(.rtl) .arrow-left{margin-left:auto;margin-right:0}:global(.rtl) .arrow-right{margin-left:0;margin-right:auto}
