.root{position:absolute;z-index:2000}.tooltip{font-family:"HelveticaNeueW01-55Roma","HelveticaNeueW02-55Roma","HelveticaNeueW10-55Roma","Helvetica Neue","Helvetica","Arial","メイリオ, meiryo","ヒラギノ角ゴ pro w3","hiragino kaku gothic pro","sans-serif";position:relative;display:inline-block;border-radius:8px;padding:12px 24px;box-sizing:border-box;font-size:14px;line-height:1.29;box-shadow:0 6px 6px 0 rgba(22,45,61,0.06),0 0 18px 0 rgba(22,45,61,0.12);word-wrap:break-word;-webkit-font-smoothing:antialiased}.tooltip.light{color:#162D3D;background:#fff}.tooltip.dark{color:#fff;background:#162D3D}.tooltip.error{color:white;background:#D6453D}.tooltip.normal{font-size:14px;line-height:18px;text-align:center}.tooltip.large{font-size:16px;line-height:24px;text-align:center}.arrow{position:absolute;height:10px;width:10px;transform:rotateZ(45deg);background:inherit}.arrow.bottom,.arrow.top{left:50%;margin-left:-5px}.arrow.bottom{bottom:-4px;box-shadow:3px 3px 6px rgba(0,0,0,0.1)}.arrow.top{top:-4px;box-shadow:-3px -3px 6px rgba(0,0,0,0.1)}.arrow.left,.arrow.right{top:50%;margin-top:-5px}.arrow.right{right:-5px;box-shadow:3px -3px 6px rgba(0,0,0,0.1)}.arrow.left{left:-5px;box-shadow:-3px 3px 6px rgba(0,0,0,0.1)}.fadeIn{animation:fadeIn 0.3s cubic-bezier(0.23, 1, 0.32, 1) both}.bounce-top,.bounce-bottom{animation:bounceVertically 1s ease infinite}.bounce-left,.bounce-right{animation:bounceHorizontally 1s ease infinite}.bounce-top:hover,.bounce-bottom:hover,.bounce-left:hover,.bounce-right:hover{animation-play-state:paused;-webkit-animation-play-state:paused;-moz-animation-play-state:paused;-o-animation-play-state:paused}@keyframes fadeIn{from{opacity:.85;transform:scale(0.9)}to{opacity:1;transform:scale(1)}}@keyframes bounceVertically{0%{transform:translateY(0)}50%{transform:translateY(-10px)}100%{transform:translateY(0)}}@keyframes bounceHorizontally{0%{transform:translateX(0)}50%{transform:translateX(-10px)}100%{transform:translateX(0)}}
